.App .expRow {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: center; */
    /* margin-right: -15px;
    margin-left: -15px; */
}

.App .expRow .expInner {
    flex: 0 0 90%;
    max-width: 90%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: flex;
    /* display: block; */
}

/* .App .expRow .expInner .expContent {
    width: 80%;
} */

.App .expRow .expInner .expContent .row {
    /* flex: 0 0 100%; */
    max-width: 100%;
    position: relative;
    width: 100%;
    /* padding-right: 15px;
    padding-left: 15px; */
    display: block;
    text-align: start;
}

.App .expRow .expInner .expContent .row h3 {
    padding-bottom: 22px;
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 0!important;
    line-height: 1.2;
    margin-top: 0;
    display: block;
    /* margin-block-start: 1em;
    margin-block-end: 1em; */
    color: white;
    padding: 0 0 40px 0;
}

.App .expRow .expInner .expContent ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: block;
    /* margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px; */
    padding-inline-start: 40px;
    text-align: left;
}

.App .expRow .expInner .expContent li {
    position: relative;
    padding: 0 20px 0 60px;
    margin: 0 0 50px;
    display: list-item;
}

.App .expRow .expInner .expContent .firstExp::after {
    content: "";
    position: absolute;
    top: 0;
    left: 20px;
    bottom: 0;
    /* border-left: 1px solid white; */
    border-left: 1px solid #333;
}

.App .expRow .expInner .expContent .expIcon {
    background-color: #7FA0C8;
    width: 40px;
    height: 40px;
    position: absolute;
    left: 0;
    right: 0;
    line-height: 40px;
    text-align: center;
    z-index: 1;
    border-radius: 50%;
    color: #fff;
    display: block;
}

.App .expRow .expInner .expContent .expIcon .innerIcon {
    /* display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased; */
    display: flex;
    justify-content: center;
    margin: 8px 0;
}

.App .expRow .expInner .expContent .expDate {
    color: #fff;
    font-size: 12px;
    padding: 1px 10px;
    display: inline-block;
    margin-bottom: 12px;
    border-radius: 20px;
    font-weight: 600;
    background-color: #252525;
    opacity: .8;
}

.App .expRow .expInner .expContent h5 {
    font-size: 18px;
    margin: 7px 0 10px;
    font-weight: 500;
    line-height: 1.2;
    display: block;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    text-transform: uppercase!important;
    color: white;
}

.App .expRow .expInner .expContent p {
    margin: 0;
    color: #eee;
    font-size: 14px;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

@media only screen and (max-width: 990px) {
    .App .expRow .expInner {
        display: flex;
        flex-direction: column;
    }
}