/* FOR SKILLS */
.App .title {
    opacity: 1;
    margin: 0 auto;
    width: 100%;
    position: relative;
    padding: 50px 0;
    transform: none !important;
    transition-delay: .1s!important;
    transition: all .6s;
    text-align: center!important;
    display: block;
    background: black;
}

/* NOTE: This is for the title text My Skills   */
.App .title h1 {
    font-size: 56px;
    font-weight: 900;
    color: white;
    text-transform: uppercase;
    margin: 0;
    line-height: 1.2;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    display: block;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    text-align: center!important;
}
  
.App .title h1 span {
    color: #7FA0C8;
}
  
/* NOTE: This is for the Resume text background */
.App .title .titleBG {
    font-size: 110px;
    left: 0;
    letter-spacing: 10px;
    line-height: 0.7;
    position: absolute;
    right: 0;
    top: 50%;
    text-transform: uppercase;
    font-weight: 800;
    transform: translateY(-50%);
    color: rgba(255, 255, 255, 0.07);
    text-align: center!important;
}
  
.App .pieContent {
    padding-bottom: 85px;
    transition-delay: 0s!important;
    opacity: 1;
    transform: none !important;
    transition: all .6s;
    /* display: block; */
    display: flex;
    justify-content: center;
}

.App .pieContent .pieContainer {
    /* border: 1px solid #252525;
    border-radius: 10px; */
    margin: 0 1rem;
    padding: 2%;
    width: 45%;
}

.App .pieContent .pieContainer .row {
    max-width: 100%;
    position: relative;
    width: 100%;
    display: block;
    text-align: center;
}

.App .pieContent .pieContainer .row h3 {
    padding-bottom: 22px;
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 0!important;
    line-height: 1.2;
    margin-top: 0;
    display: block;
    color: white;
    /* padding: 0 0 40px 40px; */
}
  
.App .pieContent .svg-item {
    max-width: 40%;
    display: inline-block;
    font-size: 16px;
    margin: 0 auto;
    animation: donutfade 1s;
}
  
@keyframes donutfade {
    0% {
        opacity: .2;
    }
    100% {
        opacity: 1;
    }
}
  
.donut-hole {
    stroke: #2F3237;
}
  
.donut-ring {
    stroke: #2F3237;
}
 
/* NOTE: This is for the design of the percentage of the circle */
.donut-segment {
    transform-origin: center;
    stroke: #FF6200;
}
  
.donut-segment-2 {
    /* stroke: aqua; */
    stroke: #7FA0C8;
    animation: donut3 3s;
}
  
.donut-segment-3 {
    stroke: #7FA0C8;
    animation: donut2 3s;
}
  
.donut-segment-4 {
    /* stroke: #ed1e79; */
    stroke: #7FA0C8;
    animation: donut1 5s;
}

/* NOTE: This is for the color of the percentage of the circle  */
.segment-1{fill:#ccc;}
.segment-2{fill:aqua;}
.segment-3{fill:#7FA0C8;}
.segment-4{fill:#ed1e79;}
  
.donut-percent {
    animation: donutfadelong 1s;
}

/* NOTE: These shows the percentage per circle   */
@keyframes donutfadelong {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
  
@keyframes donut1 {
    0% {
        stroke-dasharray: 0, 100;
    }
    100% {
        stroke-dasharray: 80, 20;
    }
}
  
@keyframes donut2 {
    0% {
        stroke-dasharray: 0, 100;
    }
    100% {
        stroke-dasharray: 30, 70;
    }
}
  
@keyframes donut3{
    0% {
        stroke-dasharray: 0, 100;
    }
    100% {
        stroke-dasharray: 50, 50;
    }
}
  
.donut-text {
    font-family: Arial, Helvetica, sans-serif;
    fill: #FF6200;
}
.donut-text-1 {
    fill: aqua;
}
.donut-text-2 {
    fill: #7FA0C8;
}
.donut-text-3 {
    /* fill: #ed1e79; */
    fill: white;
}
  
.donut-label {
    font-size: 0.28em;
    font-weight: 700;
    line-height: 1;
    fill: #000;
    transform: translateY(0.25em);
}
  
.donut-percent {
    font-size: 0.5em;
    line-height: 1;
    transform: translateY(0.5em);
    font-weight: bold;
}
  
.donut-data {
    font-size: 0.12em;
    line-height: 1;
    transform: translateY(0.5em);
    text-align: center;
    text-anchor: middle;
    color:#666;
    fill: #666;
    animation: donutfadelong 1s;
}

/* FOR MOBILE AND RESPONSIVENESS */
@media only screen and (max-width: 580px) {
    .App .title h1 {
        font-size: 46px;
    }

    .App .title .titleBG {
        font-size: 100px;
    }
}

@media only screen and (max-width: 990px) {
    .App .pieContent {
        padding-bottom: 85px;
        transition-delay: 0s!important;
        opacity: 1;
        transform: none !important;
        transition: all .6s;
        /* display: block; */
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
    }

    .App .pieContent .pieContainer {
        /* border: 1px solid #252525;
        border-radius: 10px; */
        margin: 0 1rem;
        padding: 2%;
        width: 100%;
    }
}