.App .certContainer {
    /* flex: 0 0 58.333333%;
    max-width: 58.333333%; */
    flex: 0 0 100%;
    max-width: 100%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    /* color: white; */
}

.App .certContainer .certRow {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-right: -15px;
    margin-left: -15px;
    text-align: left;
}

.App .certContainer .certRow .certInner {
    flex: 0 0 40%;
    max-width: 40%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
}

.App .certContainer .certRow .certInner .certDetails {
    margin-bottom: 30px;
    padding: 20px 30px 25px 40px;
    border-radius: 5px;
    border: 1px solid #252525;
}

.App .certContainer .certRow .certInner .certDetails .certName {
    color: #7FA0C8;;
    display: inline-block;
    margin: 0;
    font-size: 35px;
    font-weight: 700;
    position: relative!important;
    line-height: 1.2;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

.App .certContainer .certRow .certInner .certDetails .certInfo {
    padding-left: 45px;
    text-transform: uppercase!important;
    margin: 0!important;
    position: relative!important;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-size: 15px;
    color: #fff;
    font-weight: 500;
    line-height: 1.6;
}

.App .certContainer .certRow .certInner .certDetails .certInfo::before {
    content: "";
    position: absolute;
    left: 0;
    top: 13px;
    width: 30px;
    height: 1px;
    background: #777;
}

@media only screen and (max-width: 990px) {
    .App .certContainer .certRow {
        display: flex;
        /* flex-wrap: wrap;
        flex-direction: column; */
        justify-content: center;
    }

    .App .certContainer .certRow .certInner {
        flex: 0 0 80%;
        max-width: 80%;
    }
}