/* FINAL HOME */
.App .sideColor {
    background-color: #7FA0C8;
    /* position: absolute; */
    position: fixed;
    height: 200%;
    width: 100%;
    transform: rotate(-15deg);
    left: -83%;
    top: -51%;
}
  
.App .content {
    width: 100%;
    height: 100vh;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    align-items: center!important;
}
  

/* TODO: Insert background image for Home */
.App .content .myImage {
    background-image: url("https://images.unsplash.com/photo-1486246772917-8947d0f7f541?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=435&q=80");
   /* background-image: url("./assets/4c.jfif"); */ 
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    height: calc(100vh - 60px);
    z-index: 111;
    border-radius: 15px;
    left: 30px;
    top: 30px;
    box-shadow: 0 0 7px rgb(0 0 0 / 90%);
    /* position: fixed; */
    position: absolute;
    display: block;
    width: 100%;
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
}
  
.App .content .detailsContainer {
    text-align: left;
    margin-left: 33.333333%;
    flex: 0 0 66.666667%;
    max-width: 66.666667%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
}
  
.App .content .detailsContainer .details {
    max-width: 550px;
    margin: 0 auto;
    display: block;
    text-align: left;
}
  
.App .content .detailsContainer .details h6 {
    color: #eee;
    font-size: 22px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase!important;
    margin-bottom: 0!important;
    display: block;
    line-height: 1.2;
    margin-top: 0;
}
  
.App .content .detailsContainer .details h1 {
    font-size: 45px;
      line-height: 45px;
      font-weight: 700;
      margin: 18px 0 10px;
      color: #7FA0C8;
      font-family: 'Poppins', sans-serif;
      text-transform: uppercase!important;
      margin-block-start: 0.83em;
      margin-block-end: 0.83em;
      display: block;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      text-align: left;
}
  
.App .content .detailsContainer .details h1 span {
    color: #fff;
    font-weight: 600;
}
  
.App .content .detailsContainer .details p {
    margin: 10px 0 23px;
    font-size: 16px;
    line-height: 32px;
    font-family: 'Open Sans', sans-serif;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    text-align: left;
    color: #fff;
    font-weight: 500;
}
  
.App .content .detailsContainer .details a {
    background-color: #7FA0C8;
    color: #fff;
    text-decoration: none;
    box-shadow: none !important;
    border-radius: 26px;
    font-weight: 500;
    height: 46px;
    line-height: 46px;
    font-size: 15px;
    text-transform: uppercase;
    color: #fff;
    padding: 0 40px;
    outline: none !important;
    overflow: hidden;
    display: inline-block;
    position: relative;
    font-family: 'Poppins', sans-serif;
    letter-spacing: .5px;
    border: 0;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.App .content .detailsContainer .details a:hover {
    background-color: white;
    color: #7FA0C8;
}
  

/* FOR MOBILE AND RESPONSIVENESS */
@media only screen and (min-width: 990px) {
  .App .content .detailsContainer .details .imageMobile {
    display: none;
  }
}

@media only screen and (max-width: 990px) {
  .App .sideColor, .App .content .detailsContainer .details h6 {
    display: none;
  }

  .App .content .myImage {
    display: none;
  }

  .App .content .detailsContainer {
    margin: 0 20%
  }

  .App .content .detailsContainer .details {
    max-width: 550px;
    margin: 0 auto;
    display: block;
    text-align: center;
  }

  .App .content .detailsContainer .details .imageMobile {
    border-radius: 50%;
    width: 270px;
    height: 270px;
    border: 4px solid #252525;
    margin: 0 auto 25px;
  }

  .App .content .detailsContainer .details h1 {
    text-align: center;
    margin-block-start: 0;
    margin-block-end: 0;
  }

  .App .content .detailsContainer .details p {
    text-align: center;
  }
}

@media only screen and (max-width: 580px) {
  .App .header, .App .sideColor {
    display: none;
  }

  .App .content .detailsContainer .details h1 {
    font-size: 35px;
    text-align: left;
  }

  .App .content .detailsContainer .details p {
    font-size: 14px;
    text-align: left;
  }

  .App .content .detailsContainer {
    margin: 0 10%
  }

  .App .content .myImage, .App .content .detailsContainer .details .imageMobile {
    display: none;
  }
}