.App {
  text-align: center;
  min-height: 100vh;
  width: 100%;
  height: 100%;
  background-color: black;
  /* box-sizing: border-box; */
}

/* FOR NAV */
.App .header {
  position: fixed;
  right: 30px;
  bottom: 0;
  z-index: 3;
  display: flex;
  align-items: center;
  height: calc(100vh - 200px);
  top: 100px;
  opacity: 1;
  transition: opacity 0.3s;
  -webkit-transition: opacity 0.3s;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  color: #fff;
  font-weight: 500;
  line-height: 1.6;
}

.App .header .menu {
  margin: 0;
  padding: 0;
  opacity: 1;
  transform: none !important;
  transition-delay: .1s!important;
  transition: all .6s;
  display: block!important;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}

.App .header .menu .menuButton {
  width: 50px;
  height: 50px;
  list-style: none;
  position: relative;
  display: flex;
  align-items: center;
  transition: .3s;
  margin: 20px 0;
  border-radius: 50%;
  background: #2b2a2a;
}

.App .header .menu .menuButton a {
  display: block;
  padding: 0;
  width: 50px;
  height: 50px;
}

.App .header .menu .menuButton h2 {
  font-size: 15px;
  font-weight: 500;
}

.App .header .menu .menuButton h2 {
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  color: #fff;
  line-height: 50px;
  font-weight: 500;
  transition: all .3s;
  border-radius: 30px;
  padding: 0 25px 0 30px;
  height: 50px;
}

.App .header .menu .menuButton a:hover h2 {
  opacity: 1;
  right: 27px;
  margin: 0;
  text-align: center;
  border-radius: 30px 0 0 30px;
  background: #7FA0C8;
}

.App .header .menu .menuButton:hover {
  background: #7FA0C8;
}

.App .header .menu .menuButton i {
  color: #fff;
  transition: .3s;
  position: absolute;
  pointer-events: none;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: block;
  text-align: center;
  top: 13px;
}

.App .header .mobileNav {
  display: block!important;
}