.App .perInfoContent {
    /* transition-delay: .1s!important;
    transition: all .6s;
    display: block; */
}

.App .perInfoContent .perInfoContainer {
    max-width: calc(100% - 195px);
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    display: block;
}

.App .perInfoContent .perInfoContainer .perInfoRow {
    display: flex;
    /* flex-wrap: wrap; */
    margin-right: -15px;
    margin-left: -15px;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn {
    flex: 0 0 41.666667%;
    max-width: 41.666667%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
    text-align: start;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row {
    flex: 0 0 100%;
    max-width: 100%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row h3,
.App .perInfoContent .perInfoContainer .perInfoRow .secondColumn .eduColumn .row h3 {
    padding-bottom: 22px;
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 0!important;
    line-height: 1.2;
    margin-top: 0;
    display: block;
    /* margin-block-start: 1em; */
    margin-block-end: 1em;
    color: white;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row2 {
    flex: 0 0 50%;
    max-width: 50%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row2 .infoList {
    padding-left: 0;
    list-style: none;
    margin-top: 0;
    margin-bottom: 1rem;
    display: block;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row2 .infoList li {
    padding-bottom: 20px;
    display: list-item;
    /* text-align: -webkit-match-parent; */
    list-style: none;
    color: white;
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row2 .infoList li .listTitle {
    opacity: .8;  
    /* text-align: -webkit-match-parent; */
}

.App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row2 .infoList li .listAnswer {
    font-weight: 600;
    display: block!important;
    /* text-align: -webkit-match-parent; */
}

.App .perInfoContent .perInfoContainer .perInfoRow .secondColumn {
    margin-top: 0!important;
    flex: 0 0 58.333333%;
    max-width: 58.333333%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
    text-align: start;
}

.App .perInfoContent .perInfoContainer .perInfoRow .secondColumn .eduColumn {
    flex: 0 0 50%;
    max-width: 50%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    display: block;
}

.App .perInfoContent .perInfoContainer .perInfoRow .secondColumn .eduColumn .eduBox ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
}

.App .perInfoContent .perInfoContainer .perInfoRow .secondColumn .eduColumn .eduBox ul li {

}

/* FOR MOBILE AND RESPONSIVENESS */
@media only screen and (max-width: 580px) {
    .App .title h1 {
        font-size: 46px;
    }

    .App .title .titleBG {
        font-size: 100px;
    }
}

@media only screen and (max-width: 990px) {
    .App .perInfoContent .perInfoContainer .perInfoRow {
        display: flex;
        flex-direction: column;
        margin-right: -15px;
        margin-left: -15px;
    }

    .App .perInfoContent .perInfoContainer .perInfoRow .firstColumn,
    .App .perInfoContent .perInfoContainer .perInfoRow .secondColumn {
        flex: 0 0 100%;
        max-width: 100%;
        position: relative;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
        display: block;
        text-align: start;
    }

    .App .perInfoContent .perInfoContainer .perInfoRow .firstColumn .column .row h3,
    .App .perInfoContent .perInfoContainer .perInfoRow .expRow .expInner .expContent .row h3 {
    padding-bottom: 22px;
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 0!important;
    line-height: 1.2;
    margin-top: 0;
    display: block;
    /* margin-block-start: 1em; */
    margin-block-end: 1em;
    color: white;
    }
}